.App {
  text-align: left;
  background-color: rgb(250, 218, 199);
  font-family: Verdana, Tahoma, sans-serif;
  display: grid;
  grid-template-columns: repeat(1, 2fr);
  gap: 20px;
  place-items: center;
  padding: 20px;
  list-style-type: symbols();
}

.App h1{
  font-size: 22px;
}

.Oppmuntring{
  border: 3px solid rgb(157, 126, 99);
  padding: 5px;
  width: 650px;
}

.Birds{
  border: 4px solid rgb(157, 126, 99);
  padding: 5px;
  width: 650px;
}

.Birds ul{
  background-color:rgb(157, 126, 99);
}

.Guder{
  border: 3px solid rgb(157, 126, 99);
  padding: 5px;
  width: 650px;
}

.Guder h2{
  font-size: 18px;
}
